import { CacheService } from './../services/cache.service';
import { _local } from '@app/shared/utils/Storage';
import { environment } from '@env/environment';
import { HttpInterceptor, HttpRequest, HttpHandler, HttpResponse, HttpErrorResponse } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { finalize, map, tap } from 'rxjs/operators';
import { NzMessageService } from 'ng-zorro-antd/message';
import { NzNotificationService } from 'ng-zorro-antd/notification';

@Injectable()
export class DefaultInterceptor implements HttpInterceptor {
  constructor(
    private router: Router,
    private message: NzMessageService,
    private notification: NzNotificationService,
    public cache: CacheService,
  ) {}

  intercept(req: HttpRequest<any>, next: HttpHandler) {
    // Loading
    Promise.resolve().then(() => {
      this.cache.loading.next(true);
    });

    // Token
    const token = _local.get('token');
    if (token) {
      req = req.clone({
        setHeaders: {
          Authorization: `Bearer ${token}`,
        },
      });
    }

    // API
    req = req.clone({
      url: environment.api + req.url,
    });

    return next.handle(req).pipe(
      tap(
        // code
        (res) => {
          if (res instanceof HttpResponse && res.body?.code && ![0, 2].includes(res.body.code)) {
            this.message.error(res.body.msg);
            throw new Error('new Error');
          }
        },
        // 
        (error) => {
          if (error instanceof HttpErrorResponse) {
            switch (error.status) {
              case 400:
                this.message.error('');
                break;
              case 401:
                this.message.error('');
                this.cache.clearCache();
                this.router.navigate(['/blank/login']);
                break;
              case 403:
                this.message.error('');
                break;
              case 404:
                this.message.error('');
                break;
              case 405:
                this.message.error('');
                break;
              case 408:
                this.message.error('');
                break;
              case 500:
                this.notification.error('', '');
                break;
              case 503:
                this.notification.error('', '');
                break;
              case 504:
                this.notification.error('', '');
                break;
              default:
                this.notification.error('', '\n' + error.message);
                break;
            }
          }
        },
      ),
      map((res) => {
        // data
        if (res instanceof HttpResponse) {
          if (res.body?.code === 0) {
            return res.clone({ body: res.body?.data });
          } else if (res.body?.code === 2) {
            this.message.info(res.body.msg);
            return res.clone({ body: res.body?.data });
          }
        }
        return res;
      }),
      finalize(() => {
        // Loading
        this.cache.loading.next(false);
      }),
    );
  }
}
